import React from 'react'

const Technologies = () => {
    const heading = "Technology Skills"; // component header

    return (

            <section className="colorlib-skills" data-section="skills">
                <div className="colorlib-narrow-content">
                    <div className="row">
                        <div className="col-md-6 col-md-offset-3 col-md-pull-3">
                            <h2 className="colorlib-heading">{heading}</h2>
                        </div>
                    </div>
                    <div className="row">
                        <ul className="list-inline">
                            <img className="technology-icon" src='assets/images/tech/JavaScript-128.png' alt='JavaScript'/>
                            <img className="technology-icon" src='assets/images/tech/TypeScript-128.png' alt='TypeScript'/>
                            <img className="technology-icon" src='assets/images/tech/Bootstrap-128.png' alt='Bootstrap'/>
                            <img className="technology-icon" src='assets/images/tech/NodeJs-128.png' alt='NodeJs'/>
                            <img className="technology-icon" src='assets/images/tech/PostgreSQL-120x120.png' alt='PostgresQL'/>
                            <img className="technology-icon" src='assets/images/tech/Ionic-128.png' alt='Ionic'/>
                            <img className="technology-icon" src='assets/images/tech/Angular-128.png' alt='Angular'/>
                            <img className="technology-icon" src='assets/images/tech/React-128.png' alt='React'/>
                        </ul>              
                    </div>
                </div>
            </section>

    )
}

export default Technologies
  