import React, { useState, useEffect, useCallback } from "react";
import Skeleton from "react-loading-skeleton";
import axios from "axios";



// type ProjectProps = {
//   name:string,
//   description:string,
//   svn_url:string,
//   homepage:string,
//   stargazers_count:string,
//   languages_url:string,
//   pushed_at:string,
// } 



const ProjectCard = ({ value }:any) => {
  const {
    name,
    description,
    svn_url,
    homepage,
    stargazers_count,
    languages_url,
    pushed_at,
  } = value;
  
  return (
    <div className="col-md-6">
      {/* shadow-lg */}
      <div className="card p-3 mb-5   bg-white rounded">
      <div className="card-body">
          <div className="card-title h3">{name || <Skeleton />} </div>
          <div className="card-text">{(!description)?"":description || <Skeleton count={3} />} </div>
          {svn_url ? <CardButtons svn_url={svn_url} homepage={homepage} /> : <Skeleton count={2} />}
          <hr />
          {languages_url ? (
            <Language languages_url={languages_url} repo_url={svn_url} />
            ) : (
            <Skeleton count={3} />
          )}
          {value ? (
            <CardFooter star_count={stargazers_count} repo_url={svn_url} pushed_at={pushed_at} />
          ) : (
            <Skeleton />
          )}
        </div>
      </div>
    </div>
  );
};

const CardButtons = ({ svn_url,homepage }:any) => {
  if(homepage===""){
    // Create repo button
    return (
      <>
        <a href={svn_url} target=" _blank" className="btn btn-outline-secondary">
          <i className="" /> Repo
        </a>
      </>
    );
  }
  else 
  {
    // Create run and repo buttons
    return (
      <>
        <a href={homepage} className="btn btn-outline-success"  target=" _blank"    >
          <i className="" /> Run
        </a>
        <a href={svn_url} className="btn btn-outline-secondary" target=" _blank">
          <i className="" /> Repo
        </a>
      </>
    );
  }
};

const Language = ({ languages_url, repo_url }:any) => {
  const [data, setData] = useState([]);

  const handleRequest = useCallback(async () => {
    try {
      const response = await axios.get(languages_url);
      return setData(response.data);
    } catch (error) {
      console.error(error.message);
    }
  }, [languages_url]);

  useEffect(() => {
    handleRequest();
  }, [handleRequest]);

  const array = [];
  let total_count = 0;
  for (let index in data) {
    array.push(index);
    total_count += data[index];
  }

  return (
    <div className="pb-3">
      Languages:{" "}
      {array.length
        ? array.map((language) => (
            <a
              key={language} 
              className="badge badge-light card-link"
              href={repo_url + `/search?l=${language}`}
              target=" _blank"
            >
              {language}:{" "}
              {Math.trunc((data[language] / total_count) * 1000) / 10} %
            </a>
          ))
        : "No code deployed."}
    </div>
  );
};

// star_count, repo_url,
const CardFooter = ({ pushed_at }:any) => {
  const [updated_at, setUpdated_at] = useState("0 mints");

  const handleUpdatetime = useCallback(() => {
    const date = new Date(pushed_at);
    const nowdate = new Date();
    const diff = nowdate.getTime() - date.getTime();
    const hours = Math.trunc(diff / 1000 / 60 / 60);

    if (hours < 24) {
      if (hours < 1) return setUpdated_at("just now");
      let measurement = hours === 1 ? "hour" : "hours";
      return setUpdated_at(`${hours.toString()} ${measurement} ago`);
    } else {
      // const options = { day: "numeric", month: "long", year: "numeric" };
      const time = new Intl.DateTimeFormat("en-US", { day: "numeric", month: "long", year: "numeric" }).format(date);
      return setUpdated_at(`on ${time}`);
    }
  }, [pushed_at]);

  useEffect(() => {
    handleUpdatetime();
  }, [handleUpdatetime]);

  return (
    <p className="card-text">
      {/* <a
        href={repo_url + "/stargazers"}
        target=" _blank"
        className="text-dark text-decoration-none"
      >
        <span className="text-dark card-link mr-4">
          <i className="fab fa-github" /> Stars{" "}
          <span className="badge badge-dark">{star_count}</span>
        </span>
      </a> */}
      <small className="text-muted">Updated {updated_at}</small>
    </p>
  );
};

export default ProjectCard;